<template>
  <div id="app">
    <div class="column is-half is-offset-one-quarter"> 
      <h4 class="is-size-4">Pokedex</h4>
      <input class="input is-rounded" type="text" name="Busca" placeholder="Buscar Pokemon" v-model="busca">
      <button class="button is-fullwidth is-success buscarBtn" @click="buscar">Buscar</button>

      <div v-for="(poke, index) in filteredPokemons" :key="poke.url">
        <Pokemon :number="index + 1" :name="poke.name" :url="poke.url" />
      </div>

    </div>
  </div>
</template>

<script>
import axios from "axios";
import Pokemon from "./components/Pokemon.vue";

export default {
  name: 'App',
  data() {
    return {
      pokemons: [],
      filteredPokemons: [],
      busca:'',
    };
  },
  created: function () {
    console.log("server started")

    axios.get("https://pokeapi.co/api/v2/pokemon?limit=151&offset=0").then(res => {
      console.log("pokemons captured");
      this.pokemons = res.data.results;
      this.filteredPokemons = res.data.results;
    })
  },
  components: {
    Pokemon,
  },
  methods: {
    buscar: function () {
      this.filteredPokemons = this.pokemons;
      if(this.busca == '' || this.busca == ' '){
        this.filteredPokemons = this.pokemons;
      }else{
        this.filteredPokemons = this.pokemons.filter(pokemon => pokemon.name == this.busca)
      }
    }
  },
  computed: {
    // resultadoBusca: function(){
    //   if(this.busca == '' || this.busca == ' '){
    //     return ;
    //   }else{
    //     return this.pokemons.filter(pokemon => pokemon.name == this.busca)  
    //   }
    // }
},
}
</script>

